import React,{Component} from 'react';


import './Box1.css';






class Box1 extends Component {
    constructor(props){
        super(props)
        this.state = {
          weight:0,
          ans:false,
        
          feet:0,
          inch:0,
          cm:0,
          cm_show:0,
          age:0,
          activity:0,
          dweight:0,
          time:0,
          bmr:0,
          tdee:0,
          avgloss:0,
          dailycal:0,
        };
        this.handleOnChangew = this.handleOnChangew.bind(this)
        this.handleOnChangef = this.handleOnChangef.bind(this)
        this.handleOnChangei = this.handleOnChangei.bind(this)
        this.handleOnChangec = this.handleOnChangec.bind(this)
        this.handleOnChangea = this.handleOnChangea.bind(this)
        this.handleOnChangedw = this.handleOnChangedw.bind(this)
        this.handleOnChanget = this.handleOnChanget.bind(this)
        this.handleactivity = this.handleactivity.bind(this)
        this.handleans =  this.handleans.bind(this)
    }
    handleOnChangew(e){
        this.setState({weight:e.target.value})
    }
    
    handleOnChangef(e){
        this.setState({feet:e.target.value})
        var ft =  e.target.value; 
        var val1 = ft*30.48;
        var inc = this.state.inch;
        var val2 = inc*2.54;

        var c = val1+val2;
        var c_fin = Math.floor(c*10)/10;
        this.setState({cm:c,cm_show:c_fin})

    }
    handleOnChangei(e){
        this.setState({inch:e.target.value})
        var ft = this.state.feet;
        var val1 = ft*30.48;
        var inc = e.target.value;
        var val2 = inc*2.54;

        var c = val1 + val2;
        var c_fin = Math.floor(c*10)/10;

        this.setState({cm_show:c_fin,cm:c})
    }
    handleOnChangec(e){
        this.setState({cm:e.target.value,cm_show:e.target.value})
        var len = e.target.value;
        var inches = len/2.54;
        var ft = Math.floor(inches/12);
        var i = inches - (12*ft);
        i = Math.floor(i*10)/10;
        this.setState({feet:ft,inch:i})

    }
    handleOnChangea(e){
        this.setState({age:e.target.value})
    }
    handleOnChangedw(e){
        this.setState({dweight:e.target.value})
    }
    handleactivity(e){
        var x = e.target.value;
        var y =Number(x)
        this.setState({activity:y})
    }
    handleOnChanget(e){
        this.setState({time:e.target.value})
    }

    handleans(e){
        this.setState({ans:true})
        var w = this.state.weight;
        var h = this.state.cm;
        var a =  this.state.age;

        //calculating bmr
        var b = 447.593 + (9.247*w) + (3.098*h) + (4.33*a);
        var b_fin = Math.floor(b*1000)/1000;

        // calculating tdee

        var act = this.state.activity;
        var td = b*act;
        var td_fin = Math.floor(td*1000)/1000;


        // calculating avg loss per week

        var dw = this.state.dweight;
        var t = this.state.time;
        var aloss = ((w - dw)*7)/t;
        var aloss_fin = Math.floor(aloss*100)/100;

        // calculating daily cal

        var kg_to_lose = w -dw;
        var cal_to_lose = (kg_to_lose)*7700;
        var cal_lose_per_day = cal_to_lose/t;
        var cal_fin = Math.floor(cal_lose_per_day*1000)/1000;

        this.setState({bmr:b_fin,tdee:td_fin,avgloss:aloss_fin,dailycal:cal_fin})
        


    
    }

   
    render(){
        return (
            <div className="d">
             
                <div className="container">
                    <div className="row">
                        <div className="col">
                                <p className="weight">Weight</p>
                        </div>
                        <div className="col">
                            <input type="text"  value={this.state.weight} onChange={this.handleOnChangew} className="weightinp" placeholder="7" style={{textAlign:"center",height:"35px",border:"1px solid black",borderRadius:"5px"}}></input>
                            <span>Kgs</span>
                        </div>


                    </div>
                    <hr style={{width:"100%" ,color:"black",paddingTop:"0%"}} />
                    <div className="row">
                        <div className="col">
                            <p className="height">Height</p>
                        </div>
                        <div className="col">
                        <input type="text"  value={this.state.cm_show} onChange={this.handleOnChangec} className="cminp" style={{textAlign:"center",height:"35px",border:"1px solid black",borderRadius:"5px"}}></input>
                        <span>cm</span>
                        
                        <p className="or">OR</p>
                        </div>
                        

                    </div>
                    <div className="row">
                        <div className="col">
                            
                        </div>
                        <div className="col">
                        <input type="text"  value={this.state.feet} onChange={this.handleOnChangef} className="ftinp" style={{textAlign:"center",height:"35px",border:"1px solid black",borderRadius:"5px"}}></input>
                            <span>ft</span>
                        <input type="text"  value={this.state.inch} onChange={this.handleOnChangei} className="inchinp" style={{textAlign:"center",height:"35px",border:"1px solid black",borderRadius:"5px"}}></input>
                        <span>in</span>
                        </div>
                        
                    </div>
                    <hr style={{width:"100%" ,color:"black",paddingTop:"0%"}} />
                    <div className="row">
                         <div className="col">
                                <p className="age">Age</p>
                         </div>
                         <div className="col">
                         <input type="text"  value={this.state.age} onChange={this.handleOnChangea} className="ageinp" style={{textAlign:"center",height:"35px",border:"1px solid black",borderRadius:"5px"}}></input>
                        <span>Years</span>
                         </div>

                    </div>
                    <hr style={{width:"100%" ,color:"black",paddingTop:"0%"}} />
                    <div className="row">
                         <div className="col">
                                <p className="activitytype">Activity Type</p>
                         </div>
                         <div className="col">
                            <select onChange={this.handleactivity} className="dropinp" style={{height:"35px",border:"1px solid black",borderRadius:"5px"}}>
                            <option value="" disabled selected>Please Select</option>
                                <option value="1.2">Sedentary(little to no excercise + work a desk job</option>
                                <option value="1.375">Lightly Active(light excercise 1-3 days/week)</option>
                                <option value="1.55">Moderately Active(moderate excercise 3-5 days/week</option>
                                <option value="1.725">Heavy Activity(6-7 days of excercise</option>
                                <option value="1.9">Very Heavy Activity(7 days/physical job along with working out</option>
                            </select>
                         </div>

                    </div>
                    <hr style={{width:"100%" ,color:"black",paddingTop:"0%"}} />
                    <div className="row">
                         <div className="col">
                                <p className="desiredwt">Desired Weight</p>
                         </div>
                         <div className="col">
                         <input type="text"  value={this.state.dweight} onChange={this.handleOnChangedw} className="dwinp" style={{textAlign:"center",height:"35px",border:"1px solid black",borderRadius:"5px"}}></input>
                        <span style={{marginRight:"5%"}}>Kgs</span>
                         </div>

                    </div>
                    <hr style={{width:"100%" ,color:"black",paddingTop:"0%"}} />
                    <div className="row">
                         <div className="col-md-6">
                                <p className="days">In how much time do you want to reach the desired weight.</p>
                         </div>
                         <div className="col-md-6">
                         <input type="text"  value={this.state.time} onChange={this.handleOnChanget} className="timeinp" style={{textAlign:"center",height:"35px",border:"1px solid black",borderRadius:"5px"}}></input>
                        <span style={{marginRight:"5%"}}>Days</span>
                         </div>

                    </div>
                    <div className="row">
                        <button onClick={this.handleans} className="myButton">Calculate</button>
                    </div>

                </div>

                {this.state.ans && this.state.weight!=0 && this.state.time!=0 && this.state.activity!=0 && this.state.dweight!=0 && this.state.time!=0 && this.state.feet!=0 && this.state.inch!=0 && this.state.cm!=0?<div className="g">
                        <div className="container">
                            <div className="row">
                                <div className="col-md-6">
                                    <p className="bmr">Besal Metabolic Rate (BMR)</p>
                                </div>
                                <div className="col-md-6">
                                    <input type="text" className="bmrinp"  value={this.state.bmr}  style={{textAlign:"center",height:"35px",border:"1px solid black",borderRadius:"5px"}}></input>
                                    <span className="cal">Calories</span>
                                </div>

                            </div>

                            <div className="row">
                                <div className="col-md-6">
                                    <p className="tdee">Required daily calories to maintain current weight(TDEE)</p>
                                </div>
                                <div className="col-md-6">
                                <input type="text" className="tdeeinp"  value={this.state.tdee}  style={{textAlign:"center",height:"35px",border:"1px solid black",borderRadius:"5px"}}></input>
                                <span className="cal">Calories</span>
                                </div>

                            </div>

                            <div className="row">
                                <div className="col-md-6">
                                    <p className="avloss">Average weight loss per week</p>
                                </div>
                                <div className="col-md-6">
                                <input type="text" className="avlossinp"  value={this.state.avgloss}  style={{textAlign:"center",height:"35px",border:"1px solid black",borderRadius:"5px"}}></input>
                                <span className="cal">Kgs</span>
                                </div>

                            </div>

                            <div className="row">
                                <div className="col-md-6">
                                    <p className="dcalori">Daily calories required to reach desired weight in given time</p>
                                </div>
                                <div className="col-md-6">
                                <input type="text" className="dcalorinp"  value={this.state.dailycal}  style={{textAlign:"center",height:"35px",border:"1px solid black",borderRadius:"5px"}}></input>
                                <span className="cal">Calories</span>
                                </div>

                            </div>


                            <div className="row">
                                <p className="line1"><strong>1200 calories is the minimum calorie intake for women. Consuming fewer than 1200 calories can have detrimental effects on your health. Please consult a healthcare professional before doing so.</strong></p>
                                <p className="line2"><strong>Weight loss of more than 0.5 kgs per week is considered very unhealthy and is highly discouraged. Please consult a medical practitioner before planning to lose more than 0.5 kgs/week.</strong></p>
                            </div>

                        </div>
                </div>:this.state.ans?<p className="error">*Please Enter All Details</p>:null}

             

            </div>
            

        )
    }


}



export default Box1;