
import React,{Component} from 'react';
import Header from './components/Header';
import Box1 from './components/Box1';
import Box2 from './components/Box2';
import styled from 'styled-components';
import Footer from './components/Footer';
import FooterMain from './components/FooterMain';

import Popup from './components/Popup';
import './App.css';




class App extends Component {

  constructor(props){
  super(props);
  this.state = { showPopup:true };
  }

  togglePopup() {
   this.setState({
     showPopup: !this.state.showPopup
   });
 }

  render() {
    return (

      <div className="App">
      
      <div>
  
          {this.state.showPopup ?
            <Popup
              heading='Medical Disclaimer'
              text='The information on this site is not intended or implied to be a substitute for professional medical advice, diagnosis or treatment. All content, including text, graphics, images and information, contained on or available through this web site is for general information purposes only. You are encouraged to confirm any information obtained from or through this web site with a physician. Never disregard professional medical advice or delay seeking medical treatment because of something you have read on or accessed through this web site.'
              closePopup={this.togglePopup.bind(this)}
            />
            : null
          }
      </div>


     
      <Header />
      <Box1 />
      <Box2 />
      <Footer />
      <FooterMain />
</div>
 

    );
  }
}


export default App;

